.nav{
	display: flex;
	flex-direction: row;
	position: relative;
}
.burger {
	border:1px solid black;
	border-radius: 20%;
	width: 20px;
	height: 20px;
	margin:15px;
	display: none;
}
.line {
	width: 15px;
	height: 2px;
	margin: 2px;
	background-color: black;
	border: 1px solid black;
}
.menu li {
display: inline-block;
border:1px solid black;
height: 15px;
}

@media screen and (max-width:768px) {
	.burger {
		display: flex;
	}
	.menu {
		display: none;
		position: absolute;
		top: 80%;
		left: 50px;
	}
	.burger:hover+.menu{
		display: flex;
	}

	ul {
		display: flex;
		flex-direction: column;
	}
}
